<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process156"></a>

<p><br></p>
<p><span class="rvts17">教会的法律（</span><span class="rvts50">The Law of the Order</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:419,Y:112</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：魔导师雷蒙德（</span><span class="rvts20">Reimond</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找出矮人黑帮头领洛哈（</span><span class="rvts20">Lohar</span><span class="rvts21">）的非法走私活动证据，并报告给魔导师朱利安</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1121.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">该任务首先可以在进城处的魔导师那里得到，之后需要找到魔导师雷蒙（</span><span class="rvts20">Reimond</span><span class="rvts21">）进行细致的了解。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1122.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">魔导师雷蒙（位置</span><span class="rvts20">X:356,Y:77</span><span class="rvts21">）会告诉一批运往阿克斯的货物丢失了，最后发现的时候是被怀疑与浮木城西边的矮人商队有关。</span>
<br><span class="rvts21">之后也会在魔导师朱利安处得到矮人帮派的头领洛哈的一些相关信息（朱利安的位置位于</span><span class="rvts20">X:358,Y:71</span><span class="rvts21">）。</span>
<br><span class="rvts21">通过</span><span class="rvts20"></span><span class="rvts21">浮木阴影</span><span class="rvts20"></span><span class="rvts21">里面调查出的一些迹象以及进入</span><span class="rvts20"></span><span class="rvts21">失事洞穴</span><span class="rvts20"></span><span class="rvts21">后打败莫德斯，并从他房间里搜出是洛哈偷走了这批货物的证据（矮人女王的信，详见</span><span class="rvts20"></span><span class="rvts21">浮木阴影</span><span class="rvts20"></span><span class="rvts21">支线）。</span>
<br><span class="rvts21">之后，我们可以把它们交给洛哈，将使得该支线以坏的结局方式关闭（无证据）。相反的，把证据交给魔导师朱利安，他会让我们杀掉洛哈，端掉浮木城的这波矮人黑恶势力，那么该支线将以好的结局方式关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



